<script>
import Layout from "../../layouts/auth";
import {
  notificationMethods,
  authenticationMethods,
  storeActions,
} from "@/state/helpers";
import { mapState } from "vuex";

import appConfig from "@/app.config";
import { required, email } from "vuelidate/lib/validators";
import { LOGIN_MUTATION } from '@/api/graphql/mutations/auth'
/**
 * Login component
 */
export default {
  page: {
    title: "Login",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
  },
  data() {
    return {
      email: null,
      password: null,
      submitted: false,
      authError: null,
      tryingToLogIn: false,
      isAuthError: false,
      loading: false,
    };
  },
  validations: {
    email: {
      required,
      email
    },
    password: {
      required,
    },
  },
  computed: {
    ...mapState("authfack", ["status"]),
    notification() {
      return this.$store ? this.$store.state.notification : null;
    },
  },
  methods: {
    ...notificationMethods,
    ...authenticationMethods,
    ...storeActions,
    async tryToLogIn(){
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }else{
        this.loading = true
        try {
          const response = await this.$apollo.mutate({
            mutation: LOGIN_MUTATION,
            variables: {
              email: this.email,
              password: this.password
            }
          })
          const userData = {
            token: response.data.tokenAuth.token,
            refreshToken: response.data.tokenAuth.refreshToken,
            fullName: response.data.tokenAuth.fullName,
            permissions: this.formatArrayPermissions(response.data.tokenAuth.permissions)
          }
          this.login(userData)
          const currentStore = {
            name: response.data.tokenAuth.currentStore.name,
            number: response.data.tokenAuth.currentStore.id
          }
          this.changeStoreAction({
            ...currentStore
          })
          this.$router.push(
            this.$route.query.redirectFrom || {name: 'default'}
          )
          this.loading = false
        } catch (error) {
          this.loading = false
          this.isAuthError = true;
          this.authError = error.graphQLErrors[0].message
        }
      }
    },
    formatArrayPermissions( permissionsArray ){
      const permissions = []
      for (let permission of permissionsArray){
        permissions.push(permission.codename)
      }
      return permissions
    }
  },
};
</script>

<template>
  <Layout>
    <div class="row justify-content-center">
      <div class="col-md-8 col-lg-6 col-xl-5">
        <div class="card overflow-hidden">
          <div class="bg-soft bg-primary">
          </div>
          <div class="card-body pt-0" style="background-color: #220942; color: white;">
            <div>
              <router-link to="/">
                <div class="text-center">
                  <span>
                    <img src="@/assets/images/logo.png" alt/>
                  </span>
                </div>
              </router-link>
            </div>
            <b-alert
              v-model="isAuthError"
              variant="danger"
              class="mt-3"
              dismissible
              >{{ authError }}</b-alert
            >
            <div
              v-if="notification.message"
              :class="'alert ' + notification.type"
            >
              {{ notification.message }}
            </div>

            <b-form class="p-2" @submit.prevent="tryToLogIn">
              <b-form-group
                class="mb-3"
                id="input-group-1"
                label="Correo electrónico"
                label-for="input-1"
              >
                <b-form-input
                  id="input-1"
                  v-model="email"
                  type="text"
                  autofocus
                  placeholder="Ingresa tu correo electrónico"
                  :class="{ 'is-invalid': submitted && $v.email.$error }"
                  :disabled="loading"
                ></b-form-input>
                <div
                  v-if="submitted && $v.email.$error"
                  class="invalid-feedback"
                >
                  <span v-if="!$v.email.required">El email es requerido.</span>
                  <span v-if="!$v.email.email">Ingresa un email valido.</span>
                </div>
              </b-form-group>

              <b-form-group
                class="mb-3"
                id="input-group-2"
                label="Contraseña"
                label-for="input-2"
              >
                <b-form-input
                  id="input-2"
                  v-model="password"
                  type="password"
                  placeholder="Ingresa la contraseña"
                  :class="{ 'is-invalid': submitted && $v.password.$error }"
                  :disabled="loading"
                ></b-form-input>
                <div
                  v-if="submitted && !$v.password.required"
                  class="invalid-feedback"
                >
                  La contraseña es requeridad.
                </div>
              </b-form-group>
              <div class="mt-3 d-grid">
                <b-button 
                  type="submit"
                  style="background-color: #F05922;" 
                  class="btn-block"
                  :disabled="loading"
                >
                  <template v-if="loading">
                    <b-spinner small type="grow"></b-spinner>
                    Cargando...
                  </template>
                  <template v-else>
                    Iniciar Sesión
                  </template>
                </b-button
                >
              </div>
            </b-form>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>