import gql from 'graphql-tag'

const LOGIN_MUTATION = gql`mutation login($email: String!, $password: String!){
    tokenAuth(email: $email, password: $password){
        token
        payload
        refreshToken
        permissions{
          codename
          name
        }
        currentStore{
            name
            id
        }
        fullName
    }
}`

const REVOKE_TOKEN = gql`mutation RevokeToken($refreshToken: String!){
    revokeToken(refreshToken: $refreshToken){
        revoked
    }
}`

const REFRESH_TOKEN = gql`mutation RefreshToken($refreshToken: String!){
    refreshToken(refreshToken: $refreshToken){
        token
        payload
        refreshToken
    }
}`

export { 
    LOGIN_MUTATION,
    REVOKE_TOKEN,
    REFRESH_TOKEN
}